* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "montseratt", sans-serif;
}

.app {
  background-image: url("./assets/alaska.jpg");
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease-out;
}

.app.warm {
  background-image: url("./assets/summer.jpg");
}

.app.weather {
  background-image: url("./assets/weather.jpg");
}

main {
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.6));
  padding: 25px;
}

.search-box {
  width: 100%;
  margin: auto;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 20px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 12px;


  box-shadow: 0 8px rgba(0, 0, 0, 0.3);

  color: 10px solid black;
  font-size: 20px;

  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 1);
}

.location-box .location {
  margin-top: 50px;
  color: white;
  font-size: 70px;
  font-weight: 700;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: white;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rbga(50, 50, 70, 0.8);
  margin-top: 10px;
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 16px;

  padding: 15px 25px;

  color: white;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.9);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.5);
}

.weather-box .weather {
  color: white;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.home-box .home {
  margin: 0 auto;
  width: 60%;
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 16px;

  padding: 15px 25px;

  color: white;
  font-size: 70px;
  font-weight: 300;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.9);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.5);
  position: absolute;
  bottom: 100px;
  left: 300px
}
